<template>
<el-container class="wrap-content">
    <el-aside width="200px">
      <SideBar></SideBar>
    </el-aside>
    <el-container>
        <el-main>       
          <Main></Main>
        </el-main>
    </el-container>
</el-container>
</template>

<script>
import SideBar from '@/components/sideBar.vue'
import Header from '@/components/header.vue'
import Main from './main.vue'
export default {
  components:{
    SideBar,
    Header,
    Main
  
  },
}
</script>

<style scoped>
.wrap-content{
  height: 100%;
}
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
  }
   .el-header{
    height: 120px!important;
  }
  
  .el-aside {
    background-color: #337ab7;
    color: #333;
    text-align: center;
  }
  
  .el-main {
    color: #333;
    text-align: center;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }

</style>



